html {
  font-size: 62.5%;
}

body {
  background: linear-gradient(80deg, #162d2f, #25000a, #0d2436);
  background-size: 400% 400%;
  animation: animate 55s ease-in-out infinite;
}
@keyframes animate {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
