.home-page {

    .text-zone {
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 90%;
    }

    h1 {
        color: #FFFAEF;
        font-size: 63px;
        margin: 0;
        font-family: 'Futura';
        font-weight: 400;

        &::before {
            content: 'string whatUp public =';
            font-family: 'Avenir Next Condensed';
            color: #a43931;
            font-size: 18px;
            position: absolute;
            margin-top: -40px;
            left: 5px;
            opacity: 0.6;
        }

        &::after {
            content: 'returns';
            font-family: 'Avenir Next Condensed';
            color: #a43931;
            font-size: 18px;
            position: absolute;
            margin-top: 50px;
            margin-left: 20px;
            animation: fadeIn 1s 1.7s forwards;
            opacity: 0.6;
        }

        img {
            width: 53px;
            margin-left: 10px;
            opacity: 0;
            height: auto;
            vertical-align: middle;
            margin-top: -14px;
            animation: zoomInLeft 1s backwards;
            animation-delay: 1.4s;
            opacity: 1;
            transform: translateX(5px);
        }
    }

    h2 {
        color: #8d8d8d;
        margin-top: 20px;
        font-weight: 400;
        font-size: 20px;
        font-family: sans-serif;
        letter-spacing: 3px;
        animation: fadeIn 1s 1.8s backwards;
    }

    .flat-button {
        color: #a43931;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #a43931;
        margin-top: 25px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        white-space: nowrap;

        &:hover {
            background: #a43931;
            color: #000000;
        }

    }
}