@use "sass:math";

.text-animate {
    display: inline-block;
    opacity: 0;
    animation: fadeIn 1s 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
}

.text-animate-hover {
    min-width: 10px;
    display: inline-block;
    animation-fill-mode: both;

    &:hover {
        animation: heartBeat 1s;
        color: #a43931;
    }
}

@for $i from 1 through 35 {
    .text-animate._#{$i} {
        animation-delay: #{math.div($i, 10)}s;
    }
}