#app {
    height: 100%;
}

html,
body {
    position: relative;
    height: 100%;
}

.swiper {
    width: 50%;
    height: 70vh;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    background: transparent;
    overflow: hidden; // Ensure that overflowing slides are not visible
}

.swiper-slide {
    text-align: right;
    font-size: 18px;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; // Ensure slides are full width of the swiper container
    height: 100%; // Ensure slides are full height of the swiper container
    position: relative; // Ensure proper stacking of slides
}

.swiper-button-prev,
.swiper-button-next {
    display: none;
}